/*Модальные окна*/
.modal {
    min-width:70%;
    max-width: 90%;
    display: flex;
    flex-direction: column;
    position: fixed;
    top: 50%;
    left: 50%;
    background-color: var(--background-color);
    padding: 20px;
    border-radius: var(--br);
    box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.3);
    z-index: 9999;
    transform: translate(-50%, -50%);
  }
  
  .modal-header{
    width: 100%;
    display: flex;
    justify-content: space-between;
  }
  
  .modal-footer{
    display: flex ;
    align-items: center;
    gap: 10px;
  }
  
  .list-files {
    list-style: none;
    padding: 5px;
    max-height: 75vh;
    overflow-y: auto;
    overflow-x: hidden;
  }
  
  .list-files-upload{
    list-style: none;
    padding: 5px;
    max-height: 75vh;
    overflow-y: auto;
    overflow-x: hidden;
  }
  .list-files li {
    margin-bottom: 10px;
    padding: 5px;
    display: flex;
    flex-wrap: nowrap;
    word-wrap: break-word;
    align-items: center;
    background-color: var(--primary-color);
    border: 1px solid transparent;
    border-radius: var(--br);
    justify-content: space-between;
    transition: all 0.3s, color 0.3s;
  }
  .list-files-upload li{
    margin-bottom: 10px;
    padding: 5px;
    display: flex;
    flex-wrap: nowrap;
    word-wrap: break-word;
    align-items: center;
    background-color: var(--primary-color);
    border: 1px solid transparent;
    border-radius: var(--br);
    justify-content: space-between;
    transition: all 0.3s, color 0.3s;
  }
  
  .custom-file-upload:first-child {
    color: var(--background-color);
    background-color: var(--akcent-color);
    border: none;
    border-radius: var(--br);
    display: inline-block;
    padding: 6px;
    cursor: pointer;
    transition: all 0.3s, color 0.3s;
  }
  
  .custom-file-upload:not(:first-child) {
    color: var(--text-primary-color);
    background-color: var(--background-color);
    border: none;
    border-radius: var(--br);
    display: inline-block;
    padding: 6px 12px;
    cursor: pointer;
    transition: all 0.3s, color 0.3s;
  }
  
  .input-add-files {
    display: none;
  }
  
  .file-preview {
  max-width: 100px;
  height: auto;
  border-radius: var(--br);
  }
  
  .list-files li:hover {
    border: 1px solid var(--akcent-color);
    border-radius: var(--br);
    transition: all 0.3s, color 0.3s;
    cursor:pointer;
  }
  
  .list-image {
    width: 70px;
    align-self: flex-end;
  }
  
  .success {
    display: block;
    border:1px solid var(--color-succes) !important;
    color: var(--color-succes);
  }
  
  .error{
    border: 1px solid var(--color-error) !important;
    color: var(--color-error);
  }
  
  .success-message{
    display: block;
    color: var(--color-succes);
    margin-left: 10px;
  }
  .error-message {
    display: block;
    color: var(--color-error);
    margin-left: 10px;
  }
  
  .backdrop {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    z-index: 9998;
  }
  
  .button-public, .modal .close{
    background-color: var(--akcent-color);
    color: var(--background-color);
    border: none;
    border-radius: var(--br);
    padding: 10px;
    cursor: pointer;
    font-size: 14px;
    transition: background-color 0.3s, color 0.3s;
  }
  
  .button-public{
    margin-right: auto;
  }
  
  .modal .close:hover, .modal .button-public:hover{
    background-color: var(--akcent-color-2);
    color: var(--background-color);
  }
  
  .button-public:disabled{
    background-color: var(--secondary-color);
    transition: background-color 0.3s, color 0.3s;
  }
  
  .button-public:disabled:hover{
    background-color: var(--secondary-color);
    transition: background-color 0.3s, color 0.3s;
  }
  
  .styled-label{
    display: flex;
    align-items: center;
    cursor: pointer;
  }
  
  .styled-checkbox {
    appearance: none;
    width: 1.2rem;
    height: 1.2rem;
    background-color: var(--background-color);
    border: 1px solid var(--secondary-color);
    border-radius: var(--br);
    cursor: pointer;
    outline: none;
  
  }
  .styled-checkbox:hover {
  
    border: 1px solid var(--text-primary-color);
  }
  
  .styled-checkbox:checked {
    background-color: var(--akcent-color);
    border-color: var(--akcent-color);
  }
  
  .modal h2 {
    font-size: 24px;
    margin-bottom: 20px;
    color: var(--akcent-color);
  }
  .list-files::-webkit-scrollbar,
  .list-files-upload::-webkit-scrollbar {
    margin-left: 5px;
    width: 8px;
    border: 1px solid var(--secondary-color);
    border-radius: var(--br);
  }
  
  .list-files::-webkit-scrollbar-thumb,
  .list-files-upload::-webkit-scrollbar-thumb {
    background:var(--akcent-color);
    border-radius: var(--br);
  }
  
  .list-files::-webkit-scrollbar-thumb:hover,
  .list-files-upload::-webkit-scrollbar-thumb:hover {
    background:var(--akcent-color);
  }
  
  .progress-bar {
    display: flex;
    align-items:center ;
    width: 100%;
    height: 100%;
    border: 1px solid var(--primary-color);
    text-align: center;
  }
  
  .progress {
    align-self: center;
    width: 0%;
    height: 40px;
    background-color: var(--color-succes);
    border-radius: var(--br);
    transition: width 0.3s ease-in-out;
  }
  
  .progress-bar span {
    display: block;
    position: absolute;
    margin-top: 0px;
    color: var(--primary-color);
    padding-left: 12px;
  }
  .no-filters-message {
    text-align: center;
    padding: 250px;
    color: var(--akcent-color-2);
    font-size: 1.2rem;
  }
  
  .icon {
    margin-right: 8px;
    vertical-align: middle;
    color: var(--akcent-color);
  } 