/*ПЕРЕМЕННЫЕ*/

 :root{
  --primary-color:#F4F3EE ;
  --text-primary-color:#d1d0cd ;
  --akcent-color:#D4854D;
  --akcent-color-2:#A44A3F;
  --color-error: #E74C3C;
  --color-succes:#24ad5d;
  --secondary-color:  #cbe0e0;
  --text-color: #444444;
  --background-color: #FFFFFF;
  --br:6px
}

/*ОСНОВНЫЕ БЛОКИ*/
*{
  box-sizing: border-box;
}
body{
  font-family: 'Poppins', sans-serif;
  margin: 0;
  padding: 0;
  overflow: hidden;
}
#root{
  display: flex;
  font-family: 'Poppins', sans-serif;
  font-size: 14px;
  background-color: var(--primary-color);
  height: 100vh;
}
.content-container{
  height: 100vh;
  flex-basis: 100%;
  display: flex;
  gap: 10px;
  padding: 5px;
  overflow: hidden;
}

/*КОНТЕЙНЕР КАРТОЧЕК*/
.image-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
  align-self: flex-start;
  align-items: flex-start;
  gap: 10px;
  background-color: var(--background-color);
  border-radius: var(--br);
}

.wrapper-content{
  width: 75%;
  background-color: var(--background-color);
  border-radius: var(--br);
  padding: 5px;
  overflow: hidden;
}

.image-container::-webkit-scrollbar {
  width: 8px;
  border: 1px solid var(--secondary-color);
  border-radius: var(--br);
}

.image-container::-webkit-scrollbar-thumb {
  background:var(--akcent-color);
  border-radius: var(--br);
}

.image-container::-webkit-scrollbar-thumb:hover {
  background:var(--akcent-color);
  border-radius: var(--br);
}

.loading-screen{
  height: 100vh;
  color:var(--akcent-color-2);
  text-align: center;
  margin: 0 auto;
  align-items: center;
  padding-top: 150px;
  cursor: wait;
  z-index: 100000;
}
.loading {
  position: absolute;
  top:0;
  width: inherit;
  height: 100vh;
  color:var(--akcent-color-2);
  text-align: center;
  padding-top: 150px;
  cursor: wait;
  z-index: 100000;
}
.loading p{
  animation: fadeInOut 1.5s ease infinite;
  font-size: 64px;
  filter: invert(5);
}
.loading-screen p{
  animation: fadeInOut 1.5s ease infinite;
  font-size: 24px;
}
@keyframes fadeInOut {
  0% {
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}

/* Ваш файл стилей */

button {
  transition: transform 0.3s ease; /* добавляем плавный переход для анимации */
}

button:active {
  transform: scale(0.95); /* изменяем размер при нажатии */
}
