
  /*КАРТОЧКА*/
  .image-card {
    position: relative;
    align-self: flex-start;
    align-items: flex-start;
    height: 220px;
    max-width: 260px;
    background-color: var(--background-color);
    padding: 10px;
    border-radius: var(--br);
    border: 1px solid var(--secondary-color);
    box-shadow: 0px 3px 5px hsl(180, 18%, 87%, 0.6);
    cursor: pointer;
    transition: transform 0.3s ease;
    overflow: hidden;
    flex-direction: row;
  }
   
   .image-card:hover .card-title p{
    color: var(--text-color);
    background-color: var(--secondary-color);
    padding: 5px 7px;
    border-radius: var(--br);
    transition: all 0.3s ease;
    cursor: pointer;
    border: none;
  }
  .image-card:hover .icons-download{
    color: var(--background-color);
    background-color: var(--akcent-color);
    padding: 5px 7px;
    border-radius: var(--br);
    transition: all 0.3s ease;
    cursor: pointer;
    border: none;
  }
  .image-card:hover .card-size{
    color: var(--primary-color);
  }
  .image-card:hover {
    transform: translateY(0);
  }
  .card-content{
    display: flex;
    justify-content: space-between;
  }
  .image-card img {
    display: block;
    object-fit: cover;
    max-width: 100%;
    height: auto;
  }
  .placeholder{
    width: 100%;
    font-size: 40px;
    text-align: center;
    color: var(--secondary-color);
  }
  
  .card-title{
    display: flex;
    align-items: flex-start ;
    justify-content: space-between;
    margin-top: 0;
  }
  
  .card-title p {
    background-color: var(--primary-color);
    color: var(--akcent-color);
    padding: 5px 7px;
    margin-top: 0;
    border-radius: var(--br);
  }
  
  .icons-download{
    display: block;
    color: var(--akcent-color);
    background-color: var(--primary-color);
    padding: 5px 7px;
    border-radius: var(--br);
    font-size: small;
    font-weight: 600;
    transition: all 0.3s ease;
    cursor: pointer;
    border: none;
  }
  
  .card-size{
    align-self: center;
    text-align: start;
    padding: 5px;
    border-radius: var(--br);
    color: var(--text-color);
  }
  .image-details {
    display: flex;
    flex-direction: column;
    align-self: flex-end;
    word-wrap: break-word;
    gap: 5px;
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    background-color: #d8e4e4cb;
    color: var(--text-color);
    padding: 10px;
    box-sizing: border-box;
    transform: translateY(100%);
    transition: transform 0.3s ease;
    border-radius: var(--br);
  }
  .image-details p{
    margin: 0;
  }
  
  .image-card:hover .image-details {
    transform: translateY(0);
  }