/*КОНТЕЙНЕР ФИЛЬТРАЦИИ*/
.filter-container{
    width: 25%;
    padding: 5px;
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start;
    gap: 3px;
    background-color: var(--background-color);
    border-radius: var(--br);
  }
   .filter-column {
    display: flex;
    flex-direction: column;
    overflow: hidden;
  }
  
   .filter-header{
    display: flex;
    justify-content: space-between;
    padding: 5px;
    background-color: var(--secondary-color);
    border-radius: var(--br);
   }
  
   .filter-list {
    list-style: none;
    padding: 0;
    margin: 5px 0px 0px 0px;
    max-height: calc(23vh - 5px);
    overflow-y: auto;
    overflow-x: hidden;
  }
  
  .filter-list:last-child{
    margin-bottom: 5px;
  }
  
   .filter-list::-webkit-scrollbar {
    margin-left: 5px;
    width: 8px;
  }
  
  .filter-list::-webkit-scrollbar-thumb {
    background:var(--akcent-color);
    border-radius: var(--br);
  }
  
  .filter-list::-webkit-scrollbar-thumb:hover {
    background:var(--akcent-color);
  }
  
  .filter-option {
    overflow: hidden;
    cursor: pointer;
    padding: 5px;
    margin-bottom: 3px;
    background-color: var(--primary-color);
    border: none;
    border-radius: var(--br);
    white-space: nowrap; 
  }
  
  .filter-option:hover {
    background-color: var(--text-primary-color);
    transition: all 0.3s ease;
  }
  
  .filter-option.selected {
    background-color: var(--akcent-color);
    color: var(--background-color);
    border-color: transparent;
  }
  
  .seria-column,
  .comp-column {
    flex: 1 1 auto;
    width: 100%;
  }
  
  .color-column,
  .profileColor-column {
    flex: 1 1 auto;
    width: calc(50% - 5px);
  }
  
  .height-column,
  .width-column,
  .depth-column{
    flex: 1 1 auto;
    width: calc(30% - 5px);
  }
   .seria-column .filter-list {
    display: flex;
    flex-wrap: wrap;
    gap: 5px;
   }
  
   .clear-button{
    background-color: var(--background-color);
    border: none;
    border-radius: var(--br);
    font-weight: 600;
    color: var(--secondary-color);
    transition: all 0.3s ease;
    cursor: pointer;
  }
  
  .clear-button:hover{
    font-weight: 600;
    color: var(--akcent-color);
  }
  
  .clear-button.active {
    background-color: var(--background-color);
    color: var(--color-error);
    font-weight: bolder;
  }
  